body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

//setup
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

[hidden] {
  display: none;
}

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

a:focus {
  outline: thin dotted;
}

a:active,
a:hover {
  outline: 0;
}

h1 {
  font-size: 2em;
}

abbr[title] {
  border-bottom: 1px dotted;
}

b,
strong {
  font-weight: 700;
}

dfn {
  font-style: italic;
}

mark {
  background: #ff0;
  color: #000;
}

code,
kbd,
pre,
samp {
  font-family: monospace, serif;
  font-size: 1em;
}

pre {
  white-space: pre-wrap;
  word-wrap: break-word;
}

q {
  quotes: \201c \201d \2018 \2019;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

img {
  border: 0;
}

svg:not(:root) {
  overflow: hidden;
}

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0;
}

button,
input {
  line-height: normal;
}

button,
html input[type=button],
/* 1 */

input[type=reset],
input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer;
}

button[disabled],
input[disabled] {
  cursor: default;
}

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: content-box;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

textarea {
  overflow: auto;
  vertical-align: top;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body,
figure {
  margin: 0;
}

legend,
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: "";
  clear: both;
  height: 0;
}

* {
  box-sizing: border-box;
}

.visuallyHidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(00 0 0);
  overflow: hidden;
}

.App {
  text-align: left;
  height: 100vh;
  background: rgb(43, 40, 45);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

body {}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.header {
  min-height: 10vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.header h1 {
  text-align: center;
  margin: 0;
}

.container {
  width: 80%;
  margin: 0 auto;
  max-width: 1000px;
  min-width: 320px;
}

.chat {
  margin: 10px 0;
  padding: 10px;
  background: #38343b;
  border-radius: 13px;
}

.chatMessages {
  overflow: auto;
  max-height: 60vh;
  border: 1px solid white;
  padding: 5px;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
}

.chatBox {
  position: relative;
  width: 100%;
  height: 200px;
  border: 1px solid #ccc;
}

.chatmessages {
  bottom: 0;
  width: 97%;
  margin: 0 auto;
}

.name {
  margin: 0;
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
}

.dateCreated {
  text-align: right;
  font-size: 9px;
  margin: 0;
}

input,
button,
textarea {
  border: none;
}

textarea {
  padding-left: 10px;
  -moz-appearance: none;
  outline: 0px none transparent;
}

#messageInputArea {
  display: -webkit-flex;
  display: flex;
}

.cta-messageInputArea:hover,
.cta-messageInputArea:focus,
.cta-messageInputArea:active {
  background: #e6c7ef;
}

#messageInput {
  min-width: calc(100% - 37px);
  border-bottom-left-radius: 10px;
}

.cta-messageInputArea {
  width: 37px;
  padding: 0;
  border-bottom-right-radius: 10px;
  background: white;
}

.cta-1 {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  border-radius: 5px;
  margin-bottom: 30px;
  height: 30px;
  background: white;
}

.cta-1 p {
  margin: 0;
  margin-right: 5px;
}

.cta-1:hover,
.cta-1:focus,
.cta-1:active {
  background: #ab9df2;
}

.cta-2 {
  margin: 0 auto;
  border-radius: 5px;
  width: 200px;
}

.content {
  word-wrap: break-word;
  margin-left: 1rem;
}

.firstPage {
  min-height: 100vh;
  background: #e6c7ef;
  display: -webkit-flex;
  display: flex;
  margin: 0 auto;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.formContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  min-height: calc(100vh - 40px);
}

.firstPageForm {
  padding: 40px;
  min-width: 320px;
  background: #812e84;
  position: relative;
  border-radius: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-self: center;
          align-self: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  color: white;
}

.firstPageForm h1 {
  font-family: "Caveat", cursive;
  font-size: 4rem;
}

.firstPageForm h1+p {
  font-weight: bold;
}

.enterUserName {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

#userName {
  border-radius: 5px;
  width: 100%;
  text-align: center;
  height: 40px;
  margin-bottom: 1rem;
}

#userName:focus,
#userName:hover {
  background: #ab9df2;
}

.cta:focus,
.cta:hover {
  background: #ab9df2;
}

.cta {
  width: 37px;
  padding: 0;
  border-radius: 5px;
  height: 38px;
}

footer {
  height: 40px;
  width: 100%;
  background: #812e84;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.chatMessageFooter p {
  color: black;
}

footer p {
  -webkit-align-self: center;
          align-self: center;
  margin: 0;
  color: white;
}

img {
  object-fit: cover;
  width: 13%;
}

.themeColorSelection {
  display: -webkit-flex;
  display: flex;
  margin-bottom: 10px;
}

label {
  margin-bottom: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

input[type="radio"] {
  opacity: 0;
  position: absolute;
}

input#lightGreen {
  margin-right: 0;
}

label[for="orange"] {
  background: orange;
}

label[for="purple"] {
  background: #ab9df2;
}

label[for="lightGreen"] {
  background: #a9dc76;
}

label[for="PaleVioletRed"] {
  background: #ea5d86;
}

label {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  margin-right: 15px;
}

label:last-child {
  margin-right: 0;
}

input[type="radio"]:checked+label,
input[type="radio"]:hover+label,
input[type="radio"]:focus+label {
  box-shadow: 0 0 0 1px black;
}

.toggleSwitch {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.toggleSwitch p {
  margin: 0;
  padding-bottom: 6px;
  -webkit-align-self: center;
          align-self: center;
  font-size: 18px;
}

.switch {
  text-align: center;
}

.chatIcon {
  margin-left: 0.5rem;
}
